import { isAndroid, isIOS } from '@/utils/platform'
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    isFirstPage: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    goPrev () {
      if (this.isFirstPage) {
        try {
          if (isAndroid()) {
            window.glamor.closePage()
          } else if (isIOS()) {
            this.$device.JsBridge.callHandler('h5LeftBack', {}, (data) => {})
          } else {
            this.$router.go(-1)
          }
        } catch (error) {}
      } else {
        this.$router.go(-1)
      }
    }
  }
}



